.hours-page {
  font-family: 'Baskerville', serif;
  text-align: center;
  padding: 10vh 2rem;
  background-color: #f9f9f9;
}
  
.hours-page h1 {
  font-weight: 400;
  font-size: 3rem;
}

.hours-content {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  align-items: center;
}

.map-container {
  width: 100%;
  max-width: 900px;
  margin: 0 auto;
}

.hours-info {
  font-size: 1.25rem;
  color: #333;
}

.leaflet-popup-content-wrapper {
  background-color: #f9f9f9;
  box-shadow: none;
  font-family: 'Baskerville', serif;
  font-weight: 400;
  color: #333;
}

h3 {
  font-weight: 600;
}

.leaflet-popup-tip {
  background-color: #f9f9f9;
}

@media (max-width: 768px) {
  .hours-content {
    padding: 1rem;
  }
  .map-container {
    width: 100%;
    max-width: 100%;
  }
  .hours-info {
    font-size: 1rem;
  }
}
