.dining-page {
    font-family: 'Baskerville', serif;
    background-color: #f9f9f9;
    padding: 10vh 10%;
}
  
.dining-section {
    width: 100%;
    margin-bottom: 2rem;
}

.dining-section h1 {
    font-size: 3rem;
    font-weight: 400;
    text-align: left;
    margin-bottom: 2rem;
}

.dining-section .option-title {
    font-size: 1.25rem;
    font-weight: 400;
}

.dining-section .option-cost {
    font-size: 1.25rem;
    font-weight: 400;
    font-style: italic;
}

.dining-content {
    display: flex;
    gap: 2rem;
    margin-bottom: 3rem;
}

.dining-image {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.dining-image img {
    max-width: 100%;
}

.dining-list {
    flex: 3;
}

.dining-option {
    cursor: pointer;
    padding: 1rem 0;
    transition: background-color 0.3s ease, color 0.3s ease;
}

.dining-option:hover {
    background-color: #eee;
    color: #555;
}

.dining-option:active {
    background-color: #ddd;
    color: #333;
}

.dining-option-header {
    display: flex;
    justify-content: space-between;
    font-size: 1.25rem;
    font-weight: bold;
    color: #333;
}

.accordion-image {
    padding: 1rem 0;
    display: none;
}

.accordion-image img {
    width: 100%;
    border-radius: 8px;
}

.dining-option.active .accordion-image {
    display: block;
}

.dining-option hr {
    border: none;
    border-top: 1px solid #ccc;
    margin: 0.5rem 0;
}

@media (max-width: 768px) {
    .dining-content {
        flex-direction: column;
    }

    .dining-image {
        display: none;
    }

    .accordion-image {
        display: block;
    }
}
