body {
  margin: 0;
  padding: 0;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2vh 5%;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  transition: background-color 0.3s;
  z-index: 1000;
  color: white;
  background-color: transparent;
  box-sizing: border-box;
  overflow-x: hidden;
}

.navbar.scrolled {
  background-color: rgba(0, 0, 0, 0.8);
}

.navbar .logo {
  font-size: 1.5em;
  white-space: nowrap;
}

.navbar .links {
  display: flex;
  gap: 15px;
  flex-wrap: wrap;
  justify-content: flex-end;
  max-width: 100%;
  overflow: hidden;
}

.navbar .links a {
  color: white;
  text-decoration: none;
  font-size: 1em;
}

@media (max-width: 768px) {
  .navbar .links {
    display: none;
    position: absolute;
    right: 0;
    top: 60px;
    background-color: rgba(0, 0, 0, 0.8);
    width: 100%;
    box-sizing: border-box;
    padding: 10px;
    border-radius: 5px;
  }

  .navbar.scrolled .links {
    display: block;
  }

  .navbar .links a {
    display: block;
    padding: 5px 10px;
    text-align: right;
  }
}

html, body {
  overflow-x: hidden;
}
