@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400..900;1,400..900&display=swap');

body {
  margin: 0;
  font-family: 'Baskerville', serif;
}

.hero {
  width: 100%;
  height: 100vh;
  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  padding: 2rem;
  border-bottom: 1px solid #333;
}

.content {
  color: white;
  max-width: 40%;
}

.content p {
  font-size: 1.25rem;
  line-height: 1.8;
  text-align: justify;
}

.content h1 {
  font-family: 'Playfair Display', serif;
  font-size: 14rem;
  font-weight: 600;
  text-transform: uppercase;
  margin-bottom: 4%;
  margin-top: 1%;
}

.about-us {
  background-color: #f9f9f9;
  color: #333;
  border-bottom: 1px solid #333;
  padding: 10vh 10%;
}

.about-us p {
  font-size: 1.25rem;
  line-height: 1.8;
  text-align: justify;  
}

.two-column {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-family: 'Baskerville', serif;
  text-align: left;
  color:#333;
  border-bottom: 1px solid #333;
  padding: 10vh 10%;
}

.two-column .left {
  width: 50%;
}

.two-column .right {
  display: flex;
  width: 50%;
  justify-content: center;
  align-items: center;
}

.two-column img {
  width: 50%;
  height: auto;
  object-fit: cover;
}

.two-column h1 {
  font-weight: 400;
  font-size: 3rem;
  margin-bottom: 2rem;
}

.two-column p {
  font-size: 1.25rem;
  line-height: 1.8;
}

.menu {
  background-color: #f1f1f1;
  text-align: left;
  font-family: 'Baskerville', serif;
  text-align: left;
  color:#333;
  border-bottom: 1px solid #333;
  padding: 10vh 10%;
}

.menu h1 {
  font-size: 3rem;
  margin-bottom: 4rem;
  font-weight: 400;
}

.menu-items {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  gap: 1rem;
  position: relative;
}

.menu-item {
  position: relative;
  width: 200px;
  height: 300px;
  transition: transform 0.3s ease;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: float 3s ease-in-out infinite;
  overflow: hidden;
}

.menu-item:nth-child(odd) {
  animation-delay: 0.5s;
}

.menu-item:nth-child(even) {
  animation-delay: 0s;
}

.menu-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.menu-info {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.6);
  color: #f9f9f9;
  padding: 1rem;
  opacity: 0;
  transform: translateY(100%);
  transition: opacity 0.3s ease, transform 0.3s ease;
  text-align: center;
}

.menu-item:hover .menu-info {
  opacity: 1;
  transform: translateY(0);
}

.menu-info h2 {
  font-size: 1.25rem;
  margin-bottom: 1rem;
  font-weight: 400;
}

.arrow {
  font-size: 1.25rem;
  display: inline-block;
  opacity: 0;
  transform: translateX(20px);
  transition: opacity 0.3s ease, transform 0.3s ease;
}

.menu-item:hover .arrow {
  opacity: 1;
  transform: translateX(0);
}

@keyframes float {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-5px);
  }
  100% {
    transform: translateY(0);
  }
}

@media (max-width: 768px) {
.hero {
  justify-content: center;
  align-items: center;
  height: 50vh;
  width: auto;
}

.content {
  max-width: 100%;
  text-align: center;
}

.content h1 {
  font-size: 4rem;
  font-weight: 600;
  margin: 0;
}

.content p {
  display: none;
}

.about-us {
  margin: 0;
  padding: 8vh 2rem;
}

.about-text {
  font-size: 1rem;
}

.two-column {
  flex-direction: column-reverse;
  padding: 3rem 2rem;
}

.two-column img {
  width: 100%;
}

.two-column .left {
  width: 80%;
}

.two-column .right {
  width: 80%;
}

.two-column h2 {
  font-size: 2rem;
}

.two-column p {
  font-size: 1rem;
}

.menu-items {
  flex-direction: column;
  align-items: center;
}

.menu-item {
  width: 80%;
  height: 200px;
  animation: none;
}

.menu-item:nth-child(1),
.menu-item:nth-child(2),
.menu-item:nth-child(3),
.menu-item:nth-child(4) {
  top: 0;
}
}

.reviews-section {
  background-color: #f9f9f9;
  padding: 10vh 10%;
}

.reviews-section h1 {
  text-align: left;
  font-weight: 400;
  font-size: 3rem;
}

.reviews-wrapper {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 2rem;
}

.arrowb {
  position: relative;
  color: #333;
  background-color: #f9f9f9;
  border: none;
  padding: 2rem;
  font-size: 1.5rem;
  cursor: pointer;
  border-radius: 50%;
  z-index: 2;
}

.arrow.left-arrow {
  position: absolute;
  left: -3rem; /* Adjust to position the left arrow */
}

.arrow.right-arrow {
  position: absolute;
  right: -3rem; /* Adjust to position the right arrow */
}

.reviews-container {
  display: flex;
  gap: 2rem;
  overflow: hidden;
  justify-content: center;
  align-items: center;
  position: relative; /* Allows arrow placement relative to this container */
}

.review {
  flex-shrink: 0;
  width: 100%;
  text-align: center;
}

.stars {
  font-size: 1.25rem;
  color: #ffcc00;
  margin-bottom: 1rem;
}

.review p {
  font-size: 1rem;
  color: #333;
  font-family: 'Baskerville', serif;
  line-height: 1.5;
  text-align: left;
}

/* Reviews container adjustments for mobile */
@media (max-width: 768px) {
  .reviews-container {
    overflow: hidden;
    flex-direction: row; /* Maintain horizontal layout */
    justify-content: flex-start;
    max-width: 80%;
  }

  .review {
    width: 50%; /* Make the single review occupy full width */
    margin: 0;
  }

  .arrowb {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }

  .arrowb .left-arrow {
    left: 10px;
  }

  .arrowb .right-arrow {
    right: 10px;
  }
}