.about-page {
    font-family: 'Baskerville', serif;
    background-color: #f9f9f9;
}

.farm-to-table {
    padding: 10vh 10%;
    background-color: #f9f9f9;
    border-top: 1px solid #333;
    border-bottom: 1px solid #333;
}

.about-page h1 {
    font-size: 3rem;
    font-weight: 400;
    text-align: left;
    margin-bottom: 2rem;
}

.timeline {
    display: flex;
    overflow-x: auto;
    gap: 2rem;
    margin-bottom: 2rem;
}

.timeline-item {
    flex-shrink: 0;
    width: 30%;
    text-align: left;
}

.timeline-item img {
    width: 100%;
    height: auto;
}

.timeline-item p {
    font-size: 1rem;
    color: #333;
    margin-top: 1rem;
}

.about-page .two-column {
    background-color: #f1f1f1;
    border-bottom: 1px solid #333;
}